import { _decorator, Component, Node, Prefab, log } from 'cc';
import VDBaseScreen from '../../../../../vd-framework/ui/VDBaseScreen';
import VDScreenManager from '../../../../../vd-framework/ui/VDScreenManager';
const { ccclass, property } = _decorator;

@ccclass('level1_match')
export class level1_match extends Component {

    @property({type: Node})
    itemQuestions: Node[] = [];
    @property({type: Node})
    itemAnswer: Node[] = [];

    onClickBtnNext() {
        let level2_screen = VDScreenManager.instance.assetBundle.get('res/prefabs/screen/match/level2', Prefab)!;
        VDScreenManager.instance.pushScreen(level2_screen, (screen: VDBaseScreen) => { }, true);
    }
    onClickBtnBack() {
        VDScreenManager.instance.popScreen(true);
    }
    onClickInstruction() {
        log("start instruction")
    }
    start() {

    }

    update(deltaTime: number) {
        
    }
}


